html,
body {
  height: 100%;
  overflow: hidden;
  font-family: Roboto, sans-serif;
  color: white; }

header {
  position: static;
  top: 0;
  left: 0;
  right: 0;
  color: white;
  background-color: #A32638; }
  header .wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-left: 50px; }
  header .menu {
    display: flex;
    flex-direction: row; }
  header .menuitem {
    display: flex;
    flex-direction: row;
    height: calc(100% - 2px);
    padding-left: 12px;
    padding-right: 12px;
    margin-left: 12px;
    font-size: large;
    color: 'white'; }
  header .menuitem:hover {
    background-color: #1B80DE;
    text-decoration: black;
    cursor: pointer; }
  header div {
    display: block;
    color: white;
    text-align: center;
    line-height: 62px; }
  header div a:hover {
    background-color: #8d2f3d;
    text-decoration: none;
    cursor: pointer; }
  header .selected {
    border-bottom: 2px solid #ffffff; }
  header img {
    display: block;
    margin: auto;
    height: 30px; }

.menuitem {
  display: flex;
  flex-direction: row;
  height: calc(100% - 2px);
  padding-left: 12px;
  padding-right: 12px;
  margin-left: 12px;
  font-size: large;
  color: 'white'; }

.menuitem:hover {
  background-color: #1B80DE;
  text-decoration: black;
  cursor: pointer; }

.wrap {
  height: auto;
  background-color: #135124;
  overflow: hidden; }

.content {
  height: calc(100% - 60px);
  background-color: #ffffff;
  overflow-y: hidden;
  overflow-x: hidden; }

.full {
  width: 100%;
  height: 100%; }

.mainGrid {
  height: 100%; }

.sideGrid {
  width: 20%; }

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome and Opera */ }

svg text {
  cursor: default;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.MuiIconButton-root.Mui-disabled {
  background-color: #bdbdbd !important; }
